.breadcrumbs{
  width: 100%;
  max-width: 1315px;
  margin: 0 auto;
  background: $white;
  height: 40px;
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  ol{
    margin-left: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    li{
      &:after{
        content: '/';
        padding-left: 10px;
        padding-right: 10px;
        color: $gray-dark;
      }
      &:last-child{
        &:after{
          display: none;
        }
      }
    }
  }
}
.single-prod-detail{
  width: 100%;
  max-width: 1315px;
  background: $white;
  margin: 0 auto;
  border: 1px solid $light-gray;
  .detail-shop-info{
    width: 100%;
    height: 150px;
    border-bottom: 1px solid $light-gray;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 25px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    .logo-shop{
      width: 180px;
      height: 100%;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    .detail-shop-wrap{
      width: calc(100% - 220px);
      .left{
        width: 65%;
      }
      .right{
        width: 35%;
      }
      .shop-rating-wrap{
        width: 150px;
        height: 115px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .detail-rating{
          background: #fffadc;
          text-align: center;
          p{
            margin-bottom: 10px;
            margin-top: 10px;
          }
          p > span{
            font-size: 36px;
            font-weight: 500;
            color: $green;
            display: inline-block;
            margin-right: 10px;
          }
        }
        .shop-feedback{
          text-align: center;
          a{
            color: $blue-btn;
            text-decoration: underline;
          }
        }
      }
      .map{
        margin-bottom: 20px;
        i{
          display: inline-block;
          width: 20px;
          height: 20px;
          background: url("../img/png/map-orange.png") no-repeat center;
          vertical-align: sub;
        }
      }
      ul{
        height: 37px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        flex-direction: column;
        li{
          color: $gray-dark;
        }
      }
      h2{
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 15px;
      }
    }
  }
  .detail-product-wrap{
    padding: 25px 30px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .shop-gallery{
      width: 380px;
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-between;
      align-items: flex-start;
      height: 390px;
      .small-photos{
        display: flex;
        justify-content: space-between;
        width: 350px;
        margin: auto;
        position: relative;
        .small-photos-btn {
          width: 30px;
          height: 82px;
          background: #f5f7f8;
          display: block;
          position: relative;
          cursor: pointer;
        }
        .small-photos-btn-next::before {
          content: '';
          width: 14px;
          height: 14px;
          border-top: 2px solid #26c85a;
          border-right: 2px solid #26c85a;
          position: absolute;
          top: 0;
          bottom: 0;
          left: -5px;
          right: 0px;
          margin: auto;
          transform: rotate(45deg);
        }
        .small-photos-btn-prev::before {
          content: '';
          width: 14px;
          height: 14px;
          border-top: 2px solid #26c85a;
          border-right: 2px solid #26c85a;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: -5px;
          margin: auto;
          transform: rotate(-135deg);
        }
        .show-all{
          display: inline-block;
          width: 100%;
          height: 35px;
          line-height: 35px;
          text-align: center;
          background: #e2e9f0;
          -webkit-border-radius: 4px;
          -moz-border-radius: 4px;
          border-radius: 4px;
          cursor: pointer;
        }
        .single-small-photo{
          width: 80px;
          height: 80px;
          margin-bottom: 15px;
          border: 1px solid #e2e9f0;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          img{
            max-height: 100%;
            max-width: 100%;
          }
        }
        .single-small-photo.active{
          border-color: #48a2ff;
        }
      }
      .full-photo{
        text-align: center;
        max-height: 365px;
        margin: auto auto 35px auto;
        padding: 0 10px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        height: 365px;
        img{
          max-height: 100%;
          max-width: 100%;
          width: 100%;
          height: 100%;
        }
      }
      .product-options{
        width: 100%;
        border-top: 1px solid $light-gray;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .find-good{
          i{
            background: url("../img/products/eye.png") no-repeat center;
          }
        }
        .faworite{
          i{
            background: url("../img/products/faworite.png") no-repeat center;
          }
        }
        .compare{
          i{
            background: url("../img/products/compare.png") no-repeat center;
          }
        }
        div{
          width: 33%;
          border-right: 1px solid $light-gray;
          margin-top: 10px;
          min-height: 80px;
          padding: 0 15px;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          flex-direction: column;
          i{
            display: inline-block;
            width: 28px;
            height: 20px;
            margin-bottom: 10px;
            margin-top: 15px;
          }
          p{
            text-align: center;
            line-height: 16px;
          }
          &:last-child{
            border-right: none;
          }
        }
      }
    }
    .detail-description{
      width: 570px;
      h1{
        font-size: 24px;
        font-weight: 500;
        line-height: 36px;
        margin-bottom: 15px;
        display: flex;
        justify-content: space-between;
        button {
          background: #48a2ff;
          border: 1px solid #2d92fa;
          text-transform: uppercase;
          color: #fff;
          font-size: 20px;
          font-weight: 500;
          padding: 10px 30px;
          border-radius: 3px;
        }
      }
      .feedback-wrap{
        width: 100%;
        margin-bottom: 20px;
        .left{
          width: 50%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          .feedback-link{
            display: inline-block;
            margin-left: 20px;
            color: $blue-btn;
          }
        }
        .right{
          text-align: right;
        }
      }
      .price-wrap{
        width: 100%;
        margin-bottom: 40px;
        .price{
          font-size: 24px;
          font-weight: 500;
          margin-bottom: 15px;
          color: $green;
        }
        .large-btn{
          height: 50px;
          border: 1px solid $blue-btn-bd;
          background: $blue-btn;
          font-size: 20px;
          font-weight: 500;
          color: $white;
          text-transform: uppercase;
          width: 200px;
          -webkit-border-radius: 4px;
          -moz-border-radius: 4px;
          border-radius: 4px;
        }
        .right{
          text-align: right;
        }
      }
      .prod-desc-wrap{
        h2{
          font-size: 20px;
          font-weight: 500;
          margin-bottom: 15px;
        }
        p{
          line-height: 24px;
        }
        .prod-desc-wrap-link {
          color: #48a2ff;
          display: block;
          text-align: right;
          position: relative;
        }
        .arrowUp {
          display: inline-block;
          width: 16px;
          height: 9px;
          background: url(../img/product-photos/arrow.png) no-repeat center;
          transform: rotate(0deg);
          transition: all .5s;
          margin-right: 10px;
        }
        .arrowDown {
          transform: rotate(180deg);
          transition: all .5s;
        }
      }
    }
    .detail-info {
      width: 280px;
      .di-header {
        display: flex;
        justify-content: space-between;
        .di-header-id {
          font-size: 15px;
          color: #9ea4a8;
        }
        .di-header-view {
          position: relative;
          font-size: 15px;
          color: #9ea4a8;
          &:before {
            content: url("../img/products/view.png");
            position: absolute;
            left: -30px;
            width: 21px;
            height: 15px;
            bottom: 0;
            top: 2px;
            margin: auto;
          }
        }
      }
      .di-price {
        font-size: 30px;
        color: #14c44d;
        margin-top: 15px;
        font-weight: bold;
      }
      .di-buy {
        font-size: 18px;
        font-weight: bold;
        text-transform: uppercase;
        color: #fff;
        width: 100%;
        background: #48a2ff;
        border: 1px solid #2d92fa;
        border-radius: 4px;
        padding: 15px;
        margin-top: 15px;
      }
      .di-info {
        width: 100%;
        background: #f5f7f8;
        border: 2px solid #f5f7f8;
        margin-top: 20px;
        .di-info-img {
          text-align: center;
          width: 100%;
          margin: 20px auto 20px auto;
          img {
            max-width: 80%;
          }
        }
        h3 {
          font-weight: bold;
          font-size: 18px;
          text-align: center;
          color: #1dab4b;
        }
        .di-info-adres {
          color: #42b9fd;
          font-size: 18px;
          position: relative;
          padding: 15px 15px 15px 30px;
          margin-left: 20px;
          cursor: pointer;
          &:before {
            content: url("../img/products/marker.png");
            position: absolute;
            width: 12px;
            height: 18px;
            left: 0px;
            top: 5px;
            bottom: 0;
            margin: auto;
          }
        }
        .di-info-phone {
          color: #42b9fd;
          font-size: 18px;
          position: relative;
          padding: 15px 15px 15px 30px;
          margin-left: 20px;
          cursor: pointer;
          &:before {
            content: url("../img/products/phone.png");
            position: absolute;
            width: 14px;
            height: 18px;
            left: 0px;
            top: 5px;
            bottom: 0;
            margin: auto;
          }
        }
        .di-info-rate {
          background: #fff;
          padding: 15px;
          display: flex;
          justify-content: space-around;
          align-items: center;
          text-transform: uppercase;
          font-size: 13px;
          font-weight: bold;
        }
        .di-info-rate > div{
          justify-content: center;
          .rating-text {
             margin-top: 0px;
          }
        }
      }
    }
    .datail-service {
      width: 100%;
      margin-top: 100px;
      display: flex;
      .ds-pay {
        display: flex;
        flex-direction: column;
        h3 {
          font-size: 13px;
          font-weight: bold;
          text-transform: uppercase;
          padding: 15px 15px 15px 0px;
        }
        ul {
          display: flex;
          margin: 0px 15px 0px 0px;
          background: #f5f7f8;
          padding: 15px;
          border-radius: 5px;
          justify-content: space-between;
          align-items: center;
          max-height: 35px;
          li {
            margin: 0px 15px 0px 15px;
          }
        }
      }
      .del-pay {
        display: flex;
        flex-direction: column;
        h3 {
          font-size: 13px;
          font-weight: bold;
          text-transform: uppercase;
          padding: 15px 15px 15px 0px;
        }
        ul {
          display: flex;
          margin: 0px 15px 0px 0px;
          background: #f5f7f8;
          padding: 15px;
          border-radius: 5px;
          justify-content: space-between;
          align-items: center;
          max-height: 35px;
          li {
            margin: 0px 15px 0px 15px;
          }
        }
      }
    }
    .detail-post-info{
      margin-top: 35px;
      padding-top: 35px;
      border-top: 1px solid $light-gray;
      width: 100%;
      .type-payment-wrap{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 50px;
        div > p{
          margin-bottom: 15px;
        }
        div{
          .all-post-list{
            margin-right: 50px;
          }
          .all-post-list,.all-payment-list{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            div{
              width: 50px;
              height: 50px;
              margin-right: 15px;
              text-align: center;
              line-height: 50px;
              img{
                max-width: 100%;
                max-height: 100%;
              }
            }
          }
        }
      }
      .all-tabs-menu{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        .single-tab{
          position: relative;
          height: 30px;
          width: 100%;
          text-align: center;
          cursor: pointer;
          p{
            font-size: 20px;
            font-weight:500;
          }
          &:after{
            content: '';
            position: absolute;
            top: 30px;
            left: 0;
            width: 100%;
            height: 6px;
            background: $light-gray;
            display: inline-block;
          }
        }
        .single-tab.active{
          p{
            color: $green;
          }
          &:after{
            background: $green;
          }
        }
      }
      .all-tabs-detail{
        .single-tab-detail{
          margin-top: 20px;
        }
        .single-tab-detail.characteristic{
          h2{
            font-size: 20px;
            font-weight: 500;
            text-align: left;
            margin-top: 40px;
            margin-bottom: 15px;
          }
          h3{
            font-size: 16px;
            font-weight: 500;
            margin-top: 40px;
            margin-bottom: 15px;
          }
          p{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 45%;
            position: relative;
            margin-bottom: 15px;
            &:before{
              content: '';
              position: absolute;
              bottom: 0;
              left: 0;
              border: 1px dotted $light-gray;
              width: 100%;
            }
            span{
              display: inline-block;
              background: #ffffff;
              z-index: 2;
              &:last-child{
                text-align: left;
                min-width: 120px;
              }
            }
          }
        }
        .single-tab-detail.feedback-list-wrap{
          .feedback-head-wrap{
            width: 100%;
            display: flex;
            align-items: center;
            height: 100px;
            border-bottom: 1px solid $light-gray;
            h3{
              font-size: 20px;
              font-weight: 500;
              margin-bottom: 20px;
            }
            .btn{
              font-size: 20px;
              font-weight: 500;
              border: 1px solid $blue-btn-bd;
              background: $blue-btn;
              padding: 0px 20px;
              color: $white;
              -webkit-border-radius: 4px;
              -moz-border-radius: 4px;
              border-radius: 4px;
            }
            p{
              .bold{
                font-weight: 500;
                color: $gray-dark;
                margin: 0;
                cursor: default;
              }
              span{
                color: $blue-btn;
                display: inline-block;
                margin-right: 15px;
                margin-left: 15px;
                cursor: pointer;
              }
            }
            .right{
              text-align: right;
            }
          }
          .new-review{
            width: 100%;
            height: 80px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            margin-bottom: 20px;
            textarea{
              width: 60%;
              height: 100%;
              -webkit-border-radius: 4px;
              -moz-border-radius: 4px;
              border-radius: 4px;
              border: 1px solid $light-gray;
              padding-left: 10px;
              padding-top: 10px;
              -webkit-box-sizing: border-box;
              -moz-box-sizing: border-box;
              box-sizing: border-box;
            }
            .btn{
              border: 1px solid $blue-btn-bd;
              background: $blue-btn;
              color: $white;
              font-size: 20px;
              font-weight: 500;
              padding: 0 20px;
              margin-left: 15px;
            }
          }
          .all-feedback-list-wrap{
            width: 100%;
            .single-answer{
              width: 100%;
              display: flex;
              flex-direction: column;
              justify-content: flex-start;
              border-bottom: 1px solid $light-gray;
              margin-bottom: 25px;
              padding-bottom: 25px;
              -webkit-box-sizing: border-box;
              -moz-box-sizing: border-box;
              box-sizing: border-box;
              .comment-head{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                height: 80px;
                .logo-user{
                  width: 50px;
                  height: 50px;
                  -webkit-border-radius: 50%;
                  -moz-border-radius: 50%;
                  border-radius: 50%;
                  overflow: hidden;
                  margin-right: 30px;
                  img{
                    width: 100%;
                    height: 100%;
                  }
                }
                .star-rating-wrap{
                  display: flex;
                  flex-direction: column;
                  align-items: flex-start;
                  justify-content: center;
                  height: 100%;
                  .user-name{
                    font-weight: 500;
                    margin-bottom: 10px;
                  }
                  .rating-wrap{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    .rating{
                      display: inline-block;
                      margin-right: 15px;
                    }
                  }
                  .rating{
                    font-weight: 500;
                    margin-right: 15px;
                    display: inline-block;
                  }
                }
              }
              .comment-wrap{
                display: flex;
                justify-content: space-between;
                background: #f8fafb;
                padding: 10px 15px;
                min-height: 80px;
                -webkit-border-radius: 4px;
                -moz-border-radius: 4px;
                border-radius: 4px;
                .comment{
                  width: 80%;
                }
                .action-wrap{
                  width: 20%;
                  display: flex;
                  flex-direction: column;
                  justify-content: flex-start;
                  text-align: right;
                  p{
                    margin-bottom: 25px;
                    font-weight: 500;
                  }
                  .action-link{
                    color: $blue-btn;
                  }
                }
              }
              .comment-answer{
                min-height: 80px;
                position: relative;
                width: 100%;
                padding-left: 25px;
                -webkit-box-sizing: border-box;
                -moz-box-sizing: border-box;
                box-sizing: border-box;
                margin-top: 15px;
                &:before{
                  content: '';
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 3px;
                  height: 100%;
                  background: $green;
                }
                .comment-wrap{
                  background-color: transparent;
                  height: auto;
                  min-height: auto;
                }
              }
            }
          }
        }
      }
    }
  }
}

.compare-products{
  width: 100%;
  max-width: 1315px;
  margin: 0 auto;
  h2{
    text-align: center;
    margin-top: 80px;
    margin-bottom: 15px;
  }
}

.btnLink {
  padding: 10px 30px;
}


  .market-list-all{
    width: 1315px;
    margin: 40px auto 40px auto;
    background: #fff;
    border: 1px solid #d0d4dc;
    padding-top: 20px;
    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
    .single-market{
      width: 100%;
      height: 130px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid $light-gray;
      .logo-shop-wrap{
        width: 15%;
        text-align: center;
        img{
          max-width: 120px;
        }
      }
      .shop-detail-wrap{
        width: 45%;
        padding: 0 15px;
        h2{
          font-size: 20px;
          margin-bottom: 15px;
          line-height: 24px;
          text-align: left;
        }
        .star-wrap{
          display: flex;
          justify-content: flex-start;
          align-items: center;
          a{
            display: inline-block;
            margin-left: 15px;
            color: $blue-btn;
          }
        }
      }
      .price-wrap{
        width: 20%;
        .price{
          color: $green;
          font-size: 20px;
          font-weight: 500;
          margin-bottom: 15px;
        }
      }
      .go-shop-wrap{
        width: 20%;
        text-align: center;
        .btn{
          background: $blue-btn;
          border: 1px solid $blue-btn-bd;
          width: 80%;
          text-transform: uppercase;
          color: $white;
          font-size: 20px;
          font-weight: 500;
          -webkit-border-radius: 3px;
          -moz-border-radius: 3px;
          border-radius: 3px;
        }

      }
    }
    .single-market:last-child {
      border-bottom: none;
    }
  }
.market-list-all > h2 {
  font-size: 15px;
  text-align: left;
  padding-left: 40px;
  text-transform: uppercase;
  border-bottom: 1px solid #d0d4dc;
  padding-bottom: 20px;
}

.sps-contacts {
  position: fixed;
  z-index: 999;
  width: 300px;
  height: 350px;
  background: #fff;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 5px;
  border: 1px solid #d0d4dc;
  .sps-contacts-header {
    padding: 15px;
    background: #f5f7f8;
    border-bottom: 1px solid #d0d4dc;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    position: relative;
    .circle-close {
      background: #f5f7f8;
      color: #d0d4dc;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      position: absolute;
      right: 20px;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
    }
    .circle-close:hover {
      background: #1dd659;
      color: #fff;
    }
  }
  ul {
    margin-top: 20px;
    li {
      text-align: center;
    }
  }
}

.sps-adres {
  position: fixed;
  z-index: 999;
  width: 600px;
  height: 400px;
  background: #fff;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 5px;
  border: 1px solid #d0d4dc;
  .sps-adres-header {
    padding: 15px 15px 15px 40px;
    text-align: left;
    font-weight: bold;
    position: relative;
    color: #42b9fd;
    text-transform: uppercase;
    .circle-close {
      background: #f5f7f8;
      color: #d0d4dc;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      position: absolute;
      right: 20px;
      top: 0;
      bottom: 0;
      margin: auto;
      cursor: pointer;
    }
    .circle-close:hover {
      background: #1dd659;
      color: #fff;
    }
  }
  .sps-adres-header:before {
    content: url(../../../../img/products/marker.png);
    position: absolute;
    left: 15px;
  }
  ul {
    margin-top: 20px;
    li {
      text-align: center;
    }
  }
}
.overlay-contacts {
  width: 100%;
  min-height: 100%;
  background: rgba(0,0,0,.4);
  position: fixed;
  margin-top: -160px;
  z-index: 6;
}